<template>
	<div>
		<ds-nav :pagename="pagename"></ds-nav>
		<div class="page-wrapper">
			<div class="page-info uk-visible@s">
				<h1 class="page-name"> {{ pagename }} </h1>
				<div class="page-name-border" style="width: 100px;"></div>	
			</div>
			<div class="spacing"></div>
			<div class="about-container">
	    		<p>                                   &nbsp;   .</p>
				<p>       &nbsp;   &nbsp;    &nbsp;     .       <router-link :to="navigation.terms_of_service.route"> </router-link>.</p>
				<p>                          .</p>
				<p>
				          .
				</p>
				<p>     !          .              .               <router-link
				:to="navigation.data_policy.route"> </router-link> &nbsp;     .</p>
				<p>
					       &nbsp;  &nbsp;    &nbsp;     <router-link :to="navigation.disclaimer.route">  </router-link>.
				</p>
				<p>   &nbsp;     .</p>
				<p>    :)</p>
	    	</div>
		</div>
	</div>
</template>

<script>
	import Navbar from '../components/nav_bar.vue';
	
	import Alert from '../components/alert.vue';

	const navigation = require('../data/navigation.json');

	export default {
		data() {
			return {
				pagename: " ",
				navigation
			}
		},
		components: {
			'ds-nav': Navbar,
			'ds-alert': Alert
		},
		methods: {
			setPagename(name) {
				this.pagename = name
			}
		}
	}
</script>

<style>
	.spacing {
		height: 30px;
	}
	.about-container p {
		font-size: 1.5rem;
		font-family: 'Lateef', serif !important;
		text-align: justify;
		max-width: 1200px;
		font-weight: 300;
	}
	.about-container li {
		font-size: 1.1rem;
		font-family: 'Lateef', serif !important;
		margin-bottom: 10px;
	}
	.about-container a {
		font-size: 1.3rem;
		font-family: 'Lateef', serif !important;
	}
	@media screen and (max-width: 640px) {
		.spacing {
			height: 0;
		}
		.about-container {
			margin-top: -40px;
			width: 95%;
		}
		p {
			font-size: 1.2rem;
			line-height: 1.8;
			word-spacing: 0.2rem;
		}
		.about-container li {
			font-size: 1.1rem;
			margin-bottom: 10px;
		}
		.about-container a {
			font-size: 1.1rem;
		}
	}
</style>



